$bg: #E7EBF0;

$size: 10px;

.modal-template {
    margin-top: 5vh;
    margin-right: 5vw;
    margin-left: 5vw;
    background-color: white;
    border-radius: 4px;
    .modal-header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        padding-left: $size*2;
    }
    .modal-content {
        padding: $size*2;
        max-height: 90vh;
        overflow: auto;
        .carousel {
            .image {
                border-radius: 4px;
                display: flex;
                flex-direction: column;
                align-items: center;
                img {
                    max-height: 50vh;
                    width: 100%;
                    object-fit: contain;
                }
            }
        }
    }
}

.root {
    background-image: url("../img/bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.app {
    height: 100vh;
    overflow: auto;
    .home {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;
        .avatar-container {
            margin-top: 10vh;
            .avatar {
                width: 250px;
                height: 250px;
            }
        }
        .text-container {
            margin-top: 10vh;
            max-width: 1000px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;
            .name-container {
                margin-bottom: $size;
                text-align: center;
                span {
                    font-size: 3rem
                }
            }
            .moto-container {
                margin-bottom: $size;
                span {
                    font-size: 1.5rem
                }
            }
            .skill-container {
                text-align: center;
                .skill {
                    display: inline;
                }
            }
        }
    }
    .project {
        padding: $size*2;
        min-height: 100vh;
        background-color: $bg;
        .card-content {
            padding-bottom: 0;
        }
        .card-footer {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }
    }
    .scholl {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100vh;
        .container {
            margin-top: 25vh;
            max-width: 1000px;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;
        }
    }
    .contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100vh;
        .container {
            margin-top: 40vh;
            max-width: 1000px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;
            a:not(:last-child) {
                margin-bottom: $size;
            }
            a {
                text-decoration: none;
                button {
                    width: 20vh;
                }
            }
        }
    }
}